:root {
  --font-body: "Epilogue", sans-serif;
}

:root {
  --almost-white: hsl(0, 0%, 98%);
  --medium-gray: hsl(0, 0%, 41%);
  --almost-black: hsl(0, 0%, 8%);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 18px;
}

body {
  background-color: var(--almost-white);
  font-family: var(--font-body);
  overflow: hidden;
}

img {
  vertical-align: middle;
}

.title {
  font-size: 5rem;
  font-weight: 700;
  color: var(--almost-black);
  margin-bottom: 60px;
}

.text {
  max-width: 430px;
  font-size: 18px;
  font-weight: 500;
  color: var(--medium-gray);
  line-height: 1.6;
  margin-bottom: 50px;
}

.flex {
  display: flex;
}

.header {
  padding: 40px 40px;
  font-weight: 500;
}

.navbar-container {
  width: 100%;
}

.container {
  max-width: 1150px;
  margin-top: 0;
  margin-left: 15vw;
  max-height: 90vh;
}

.hero-copy {
  max-width: 600px;
  margin-top: 140px;
}

.bordered {
  padding: 10px 20px;
  border: 3px solid var(--medium-gray);
  border-radius: 12px;
}

.bordered:hover {
  border-color: var(--almost-black);
}

.btn {
  cursor: pointer;
  border: none;
  background: transparent;
}

.learn-btn {
  padding: 20px 30px;
  border-radius: 12px;
  background-color: var(--almost-black);
  border: 2px solid var(--almost-black);
  color: var(--almost-white);
  font-weight: 700;
  transition: background-color 0.3s ease;
}

.learn-btn:hover {
  background-color: var(--almost-white);
  color: var(--almost-black);
}

.burger-btn {
  display: none;
  position: relative;
  z-index: 2;
}

.hero-copy::after {
  content: "";
  height: 640px;
  width: 480px;
  background: url("../images/image-hero-desktop.png") no-repeat;
  background-size: cover;
  position: absolute;
  top: 180px;
  right: 10vw;
  bottom: 30vh;
}

.clients {
  margin-top: 100px;
}

.clients img + img {
  margin-left: 35px;
}

@media screen and (max-width: 1425px) {
  .container {
    margin-left: 5vw;
  }

  .hero-copy::after {
    right: 5vw;
  }
}
@media screen and (max-width: 1200px) {
  .container {
    margin-left: 20px;
  }

  .hero-copy::after {
    right: 20px;
  }
}
@media screen and (max-width: 1125px) {
  .container {
    margin-left: 20px;
  }

  .hero-copy::after {
    right: 20px;
  }
}
@media screen and (max-width: 1124px) {
  .container {
    max-width: 100%;
    padding: 0 15px;
  }

  .hero-copy {
    text-align: center;
    margin-top: 50vh;
    max-width: none;
  }

  .hero-copy::after {
    content: none;
  }

  .hero-copy::before {
    content: "";
    height: 50vh;
    width: 100%;
    background: url("../images/image-hero-mobile.png") no-repeat;
    background-size: cover;
    position: absolute;
    top: 100px;
    left: 0;
    right: 0;
  }

  .title {
    font-size: 2.4rem;
    padding-top: 10vh;
  }

  .title, .text {
    margin-bottom: 25px;
  }

  .text {
    max-width: none;
    margin: 0 15vw 5vh 15vw;
  }

  .btn.learn-btn {
    padding: 10px 20px;
  }

  .clients {
    margin-top: 45px;
    text-align: center;
  }

  .clients img {
    width: 64px;
  }

  .clients img + img {
    margin-left: 25px;
  }
}
.dropdown {
  position: relative;
}

.dropdown.open .dropdown-menu {
  display: block;
}

.dropdown.open .dropdown-toggle img {
  transform: rotateX(180deg);
}

.dropdown-menu {
  display: none;
  width: max-content;
  padding: 25px;
  border-radius: 12px;
  background-color: white;
  box-shadow: 0 8px 12px 12px #ededed;
  position: absolute;
  top: 40px;
}

#features {
  right: 0;
}

.dropdown-menu li + li {
  margin-top: 15px;
}

.dropdown-item img {
  margin-right: 10px;
}

.attribution {
  font-size: 14px;
  text-align: center;
}

.attribution a {
  color: #3e52a3;
}

footer {
  position: fixed;
  bottom: 0;
  text-align: center;
  width: 100%;
  height: 20px;
}

.nav-links {
  margin-left: 60px;
}

.menu-list, .dropdown-menu {
  list-style: none;
}

.menu-item + .menu-item {
  margin-left: 40px;
}

.menu-link, .dropdown-item {
  text-decoration: none;
  color: var(--medium-gray);
}

.menu-link img {
  margin-left: 5px;
}

.menu-link:hover {
  color: var(--almost-black);
}

.login-nav {
  margin-left: auto;
}

@media screen and (max-width: 925px) {
  .header {
    padding: 20px 15px;
  }

  .navbar {
    justify-content: space-between;
    align-items: center;
  }

  .burger-btn {
    display: block;
  }

  .navbar-container, .menu-list {
    flex-direction: column;
  }

  .navbar-container {
    height: 100vh;
    width: 240px;
    padding: 25px;
    position: absolute;
    top: 0;
    bottom: 0;
    right: -240px;
    background-color: #fff;
    z-index: 1;
    transition: right 0.3s ease-out;
  }

  .navbar-container.show {
    right: 0;
  }

  .nav-links, .login-nav, .menu-item + .menu-item {
    margin-left: 0;
  }

  .nav-links, .login-nav {
    margin-top: 60px;
  }

  .menu-item + .menu-item {
    margin-top: 25px;
  }

  .login-nav {
    align-items: center;
  }

  .dropdown-menu {
    background-color: transparent;
    box-shadow: none;
    position: static;
  }
}